>\r\n                <a href=\"/login/host\">\r\n                    <img src=\"/assets/manager_tools/3.png\" alt=\"\" />\r\n                    <p>Tasklist</p>\r\n                    <p>Create your own to-do list</p>\r\n                </a>\r\n            </div>\r\n        </div>\r\n        <div id=\"section-3\" class=\"container-fluid position-relative\">\r\n            <h2>Create your own invitation</h2>\r\n            <h3>\r\n                Creating moments to last a lifetime.Provide every service\r\n                related to the function of marriage\r\n            </h3>\r\n            <div class=\"container-md\">\r\n                <div class=\"row\">\r\n                    <div class=\"col-sm-6\">\r\n                        <img\r\n                            style=\"width: 100%\"\r\n                            src=\"/assets/manager_tools/Group_1114.svg\"\r\n                            alt=\"\"\r\n                        />\r\n                    </div>\r\n                    <div class=\"col-sm-6\">\r\n                        <div class=\"section-3-content\">\r\n                            <h3>GET MORE DONE</h3>\r\n                            <h1>Create</h1>\r\n                            <h1>Organize your Wedding</h1>\r\n                            <h1>Complete more task every day.</h1>\r\n                            <p>\r\n                                Lorem ipsum dolor sit amet, consectetur\r\n                                adipiscing elit. Sollicitudin vitae, posuere a\r\n                                varius ac nunc posuere. Tortor neque aliquam\r\n                                vitae, malesuada magna. Ac at et convallis\r\n                                elementum euismod nunc habitant.\r\n                            </p>\r\n                            <a href=\"/login/host\">Create invitation</a>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <div id=\"section-4\" class=\"container-fluid position-relative\">\r\n            <h2>Save the date</h2>\r\n            <h3>\r\n                Provide every service related to the function of marriage,Should\r\n                be performed with happiness\r\n            </h3>\r\n            <div class=\"container-md\">\r\n                <div class=\"section-4-content\">\r\n                    <div>\r\n                        <img src=\"/assets/manager_tools/12.svg\" alt=\"\" />\r\n                        <h3>2019</h3>\r\n                        <h4>How we met</h4>\r\n                    </div>\r\n                    <div>\r\n                        <img src=\"/assets/manager_tools/13.svg\" alt=\"\" />\r\n                        <h3>2020</h3>\r\n                        <h4>First meet</h4>\r\n                    </div>\r\n                    <div>\r\n                        <img src=\"/assets/manager_tools/11.svg\" alt=\"\" />\r\n                        <h3>2021</h3>\r\n                        <h4>the day we exchanged hearts</h4>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <div id=\"section-5\" class=\"container-fluid position-relative\">\r\n            <div>\r\n                <img\r\n                    style=\"width: 100%\"\r\n                    src=\"/assets/manager_tools/section-5.png\"\r\n                    alt=\"\"\r\n                />\r\n                <a href=\"/login/host\">Create ceremonies</a>\r\n            </div>\r\n            <div>\r\n                <h1>Create and organise your ceremonies</h1>\r\n                <p>\r\n                    Lorem ipsum dolor sit amet, consectetur adipiscing elit.\r\n                    Sollicitudin vitae, posuere a varius ac nunc posuere. Tortor\r\n                    neque aliquam vitae, malesuada magna. Ac at et convallis\r\n                    elementum euismod nunc habitant.\r\n                </p>\r\n                <a href=\"/login/host\">Create ceremonies<